.answers {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.answersBtn {
    width: 30px;
    height: 30px;

    color: #fff;

    background-color: #0067d4;
}

.deleteBtn {
    margin-left: 10px;

    background-color: #cb375a;
}

.submitBtn {
    padding: 15px 30px;

    color: #fff;

    background-color: #0067d4;
}

.goBtn {
    padding: 15px 30px;
    margin-left: 15px;

    color: #fff;

    background-color: #316b4e;
}

.doMainBtn {
    padding: 10px 25px;

    color: #fff;

    background-color: #316b4e;
}

.undoMainBtn {
    padding: 10px 25px;
    margin-left: 10px;

    color: #fff;

    background-color: #cb375a;
}

@media (max-width: 600px) {
    .undoMainBtn {
        margin-left: 0;
        margin-top: 10px;
    }

    .goBtn {
        margin-left: 0;
        margin-top: 10px;
    }
}